import Vue from "vue";
import App from "./App";
import router from "./router";

// 
import components from "@/plugin/index.js"; // 
import YaoDo from "../core/Lib/YaoDo/YaoDo.min.js";
import VGEEarth from "../core/Lib/VGEEarth/VGEEarth.min.js";//  

import "@/assets/global/css/style.css"; //  CSS
import "@/assets/global/js/index.js";  // 

//  Vue 
import Vuex from "vuex";  //  VueX
import Element from "@/plugin/Element.js"; //  Element

Vue.use(Vuex);
Vue.use(Element);


import store from "@/store/index.js"; //  Vuex store
import clientWebSocketTool from "@/assets/global/js/clientWebSocketTool.js";

window.clientWebSocketTool = clientWebSocketTool;


Vue.config.productionTip = false; //  warning 

new Vue({
    el: "#app",
    router,
    components: {App},
    created() {
        clientWebSocketTool.connect();
    },
    destroyed() {
        clientWebSocketTool.close();
    },
    template: "<App/>"
});



